.button {
  display: block; 

  padding: 14px 12px;

  outline: none;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;

  text-transform: uppercase;
  text-align: center;
  box-shadow: 0 2px 8px rgba(255, 107, 1, 0.5), inset 0 1px rgba(255, 255, 255, 0.3),
  inset 0 10px rgba(255, 255, 255, 0.2), inset 0 10px 20px rgba(255, 255, 255, 0.25),
  inset 0 -15px 30px rgba(255, 107, 1, 0.3);

  margin-left: 20px;
  min-width: 130px;
  
  color: var(--button-text-color);

  &:hover,
  &:focus,
  &:focus-visible,
  &:active {
    background-color: var(--button-color);
    color: var(--button-text-color);
    box-shadow: 0px 8px 43px rgba(255, 107, 1, 0.6);
    transition: .5s;
  }
}

.button-active {
  background: var(--button-color);
  border: var(--button-color);

  border-radius: 5px;
  color: var(--button-text-color);
}

.no-active {
  background-color: transparent;

  border-radius: 5px;
  // border: 1px solid var(--button-text-color);
  border: none;
}

.btn-lib {
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  &::before {
  content: '';
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, transparent, rgba(255,255,255,0.5), transparent);
  transform: translateX(-100%);
  transition: transform 0.4s;
  }
  &:hover::before {
    transform: translateX(100%);
  }
}

.heartbeat {
  -webkit-animation: heartbeat 1.5s ease-in-out infinite both;
  animation: heartbeat 1.5s ease-in-out infinite both;
}

@keyframes heartbeat {
  from {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-transform-origin: center center;
    transform-origin: center center;
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
  10% {
    -webkit-transform: scale(0.91);
    transform: scale(0.91);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  17% {
    -webkit-transform: scale(0.98);
    transform: scale(0.98);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
  33% {
    -webkit-transform: scale(0.87);
    transform: scale(0.87);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  45% {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
}
